import React, { Component } from 'react';
import './App.css';
import { css, StyleSheet } from 'aphrodite/no-important';
import { SyncLoader } from 'react-spinners';

class App extends Component {
  state = {
    images: [],
    lightboxIsOpen: false,
		currentImage: 0,
		selectedOption: '1',
		isLoading: false
  };
  componentDidMount() {
    this.setState({
      images: this.getImages(
        require.context('./images/original', false, /00\.png$/), 
        require.context('./images/gray', false, /00\.png$/)),
      lightboxIsOpen: true
    })
  }
  openLightbox = (index, event) => {
		event.preventDefault();
		this.setState({
			currentImage: index,
			lightboxIsOpen: true,
		});
	}
	closeLightbox = () => {
		this.setState({
			currentImage: 0,
			lightboxIsOpen: false,
		});
  }
  gotoPrevious = () => {
		this.setState({
			currentImage: this.state.currentImage - 1,
		});
	}
	gotoNext = () => {
		this.setState({
			currentImage: this.state.currentImage + 1,
		});
		let _id = this.state.images[this.state.currentImage].src.split("/")
		_id = _id[_id.length - 1].split(".")[0]
		this.setState({
			isLoading: true
		}, () => {
			fetch('http://localhost:8080/post', {
				method: 'POST',
				headers: {
					'Accept': 'application/json',
					'Content-Type': 'application/json',
				},
				body: JSON.stringify({ 
					id: _id, 
					selected: this.state.selectedOption, 
					time: new Date()
				})
			}).then(() => {
				setTimeout(() => {
					this.setState({
						isLoading: false,
						selectedOption: '1'
					})
				}, 1000)
				
			}).catch((e) => {
				console.log(e)
				this.setState({
					isLoading: false,
					selectedOption: '1'
				})
			})
		})
		
	}
	gotoImage = (index) => {
		this.setState({
			currentImage: index,
		});
	}
	handleHover = () => {
		let temp = this.state.images[this.state.currentImage]['src']
    this.state.images[this.state.currentImage]['src'] = this.state.images[this.state.currentImage]['src2']
    this.state.images[this.state.currentImage]['src2'] = temp
    //this.gotoNext();
    this.setState({
      images: this.state.images
    })
	}
	handleOptionChange = (changeEvent) => {
		this.setState({
			selectedOption: changeEvent.target.value
		})
	}
  getImages = (r, r2) => {
    let images = [];
    for (let i=0; i<10; i++) {
      images.push({
        src: r(r.keys()[i]),
        src2: r2(r2.keys()[i])
      })
    }
    return images;
  }
  renderPair = () => {
    const gallery = this.state.images.map((obj, index) => {
      return (
				<div>
					{ this.state.currentImage > 0 ? (
						<button className={css(classes.iconPrev)} onClick={this.gotoPrevious}>
							<span>Prev</span>
						</button>
					) : null}
					<img 
						className={'Img-container'} 
						onMouseEnter={this.handleHover}
						onMouseLeave={this.handleHover}
						onClick={this.handleClickImage} 
						src={obj.src} 
					/>
					{ this.state.currentImage < this.state.images.length && !this.state.isLoading ? (
						<button className={css(classes.iconNext)} onClick={this.gotoNext}>Next</button>
					) : (
							<SyncLoader
							sizeUnit={"px"}
							css={{position: 'absolute',
							top: '50%',
							right: '10%',}}
							size={20}
							color={'#38d8ab'}
							loading={this.state.isLoading}
							/>)
					}
				</div>
      )
    })
    return (
			<div
				style={{
					'display': 'flex',
					'justifyContent': 'center',
					'alignItems': 'center'
				}}
			>
				{gallery[this.state.currentImage]}
			</div>
		); 
  }
  render() {
		
    return (
      <div
				style={{
					display: 'flex',
					flexDirection: 'column'
				}}
			>
				<div className={'App-header'}>
					<div className="radio">
						<label>
							<input type="radio" value="1" checked={this.state.selectedOption === '1'} onChange={this.handleOptionChange}/>
								Option 1
						</label>
					</div>
					<div className="radio">
						<label>
							<input type="radio" value="2" checked={this.state.selectedOption === '2'} onChange={this.handleOptionChange}/>
								Option 2
						</label>
					</div>
				</div>
				<div className={'App-section'}>
					{ this.state.currentImage < this.state.images.length ? this.renderPair() : (
						<span style={{ fontSize: '20px', fontWeight: 'bold'}}>Finished !!!!</span>
					)}
				</div>
      </div>
    );
  }
}

const gutter = {
	small: 2,
	large: 4,
};

const classes = StyleSheet.create({
	header: {
		display: 'flex',
		flexGrow: 1,
		height: '100px'
	},
	iconPrev: {
		background: '#4286f4',
		position: 'absolute',
		top: '50%',
		left: '10%',
		width: '100px',
		height: '50px',
		borderRadius: '10px'
	},
	iconNext: {
		background: '#4286f4',
		position: 'absolute',
		top: '50%',
		right: '10%',
		width: '100px',
		height: '50px',
		borderRadius: '10px'
	},
	gallery: {
		marginRight: -gutter.small,
		overflow: 'hidden',

		'@media (min-width: 500px)': {
			marginRight: -gutter.large,
		},
	},

	// anchor
	thumbnail: {
		boxSizing: 'border-box',
		display: 'block',
		float: 'left',
		lineHeight: 0,
		paddingRight: gutter.small,
		paddingBottom: gutter.small,
		overflow: 'hidden',

		'@media (min-width: 500px)': {
			paddingRight: gutter.large,
			paddingBottom: gutter.large,
		},
	},

	// orientation
	landscape: {
		width: '30%',
	},
	square: {
		paddingBottom: 0,
		width: '40%',

		'@media (min-width: 500px)': {
			paddingBottom: 0,
		},
	},

	// actual <img />
	source: {
		border: 0,
		display: 'block',
		height: 'auto',
		maxWidth: '100%',
		width: 'auto',
	},
});

export default App;
